@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&family=Poppins:wght@600&display=swap");
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.flex {
  display: flex;
}

.jc-c {
  justify-content: center;
}

.ai-c {
  align-items: center;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

html {
  font-size: 1rem;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  margin: 0;
  padding: 0;
}

.bg-wrapper {
  background-image: url(/app/asset/images/bg-hero-mobile.svg);
  background-repeat: no-repeat;
  background-color: #ecfbfe;
  background-size: cover;
}
@media (min-width: 40em) {
  .bg-wrapper {
    background-image: url(/app/asset/images/bg-hero-desktop.svg);
  }
}

.footer {
  padding-block: 7.7777777778rem;
  padding-inline: 0.8333333333rem;
  background-color: #00252e;
  color: white;
}
@media (min-width: 40em) {
  .footer {
    padding-inline: 4.4444444444rem;
    padding-block: 7.7777777778rem 0;
  }
}
.footer__logo {
  display: block;
}
.footer__logo path {
  fill: #ebfbff;
}
@media (min-width: 40em) {
  .footer__logo {
    margin-bottom: 2.5rem;
  }
}
.footer__content {
  flex-flow: column nowrap;
}
@media (min-width: 40em) {
  .footer__content {
    font-size: 0.8888888889rem;
    flex-flow: row nowrap;
    align-items: flex-start;
    gap: 7rem;
  }
}
.footer__contact {
  flex-flow: column nowrap;
  justify-content: flex-start;
  gap: 0.5555555556rem;
  padding: 0;
  margin-top: 2.2222222222rem;
  margin-bottom: 3.3333333333rem;
}
.footer__contact p, .footer__contact img {
  display: inline;
}
.footer__contact img {
  margin-right: 1.3888888889rem;
}
@media (min-width: 40em) {
  .footer__contact {
    flex: 1 1 33%;
    margin: 0;
  }
}
.footer__links {
  flex-flow: column nowrap;
  justify-content: space-around;
  gap: 2rem;
}
@media (min-width: 40em) {
  .footer__links {
    flex: 1 1 33%;
    flex-flow: row nowrap;
    align-items: flex-end;
    gap: 14rem;
  }
}
.footer__box {
  flex: 1 1 50%;
  flex-flow: column nowrap;
  align-self: flex-start;
  gap: 0.4444444444rem;
}
.footer__box a {
  color: white;
  transition: all 350ms ease-in-out;
}
.footer__box a:hover {
  color: #ff52bf;
}
.footer__socials {
  margin-block: 2.8888888889rem;
}
.footer__socials ul {
  padding: 0;
  margin: 0;
}
.footer__socials li {
  transition: all 350ms ease-in-out;
}
.footer__socials li:hover {
  cursor: pointer;
  border: 1px solid #ff52bf;
  color: #ff52bf;
}
@media (min-width: 40em) {
  .footer__socials {
    flex: 1 1 33%;
    margin-top: 0;
  }
}
.footer__credit {
  font-size: 0.6666666667rem;
  text-align: center;
  margin-top: 2rem;
}
.footer__credit a {
  color: #ff52bf;
  border-bottom: 1px solid #ff52bf;
  transition: all 350ms ease-in-out;
}
.footer__credit a:hover {
  color: #ff99da;
  border-bottom: 1px solid #ff99da;
}

.header {
  padding-block: 1.4444444444rem;
  padding-inline: 0.8888888889rem;
  margin-bottom: 5rem;
}
@media (min-width: 40em) {
  .header {
    padding-block: 3.0555555556rem;
    padding-inline: 4.1666666667rem;
  }
}
.header nav {
  justify-content: space-between;
}
.header__logo img {
  width: 5.8888888889rem;
  height: 1rem;
}
@media (min-width: 40em) {
  .header__logo img {
    width: 13.8888888889rem;
    height: 2.2222222222rem;
  }
}
.header__cta {
  font-size: 0.6666666667rem;
  font-weight: 600;
  width: 6.3888888889rem;
  border: 0 solid none;
  border-radius: 5.5555555556rem;
  box-shadow: 0 8px 30px rgba(193, 200, 209, 0.477);
  background-color: #fff;
  padding: 0.5555555556em;
  transition: color 350ms ease-in-out;
}
.header__cta:hover {
  cursor: pointer;
  color: #808d99;
  transition: all 250ms;
}
.header__cta:active {
  transform: scale(0.95);
}
@media (min-width: 40em) {
  .header__cta {
    font-size: 0.8888888889rem;
    width: 10.5555555556rem;
    padding: 0.8888888889em;
  }
}

.primary {
  flex-flow: column nowrap;
  gap: 1.65rem;
  text-align: center;
  padding-inline: 0.8888888889rem;
  padding-bottom: 2.6666666667rem;
}
@media (min-width: 40em) {
  .primary {
    flex-flow: row nowrap;
    text-align: left;
    padding-inline: 2.7777777778rem;
    padding-bottom: 68px;
  }
}
.primary__container {
  flex: 1 1 50%;
}
@media (min-width: 40em) {
  .primary__container h1, .primary__container p {
    padding: 0;
  }
  .primary__container h1 {
    line-height: 1.5;
  }
}
.primary__heading {
  font-size: min(max(1.52rem, 4vw), 3.5rem);
  padding-inline: 2.4444444444rem;
  line-height: 1.6;
}
.primary__subtitle {
  padding-inline: 1.3333333333rem;
  margin-block: 1.1111111111rem 1.9444444444rem;
}
.primary__cta {
  margin-bottom: 3.1111111111rem;
}
.primary__hero {
  flex: 1 1 50%;
  padding-inline: 0.5555555556rem;
}
.primary__hero img {
  width: 100%;
}
@media (min-width: 40em) {
  .primary__hero img {
    max-width: 52.7777777778rem;
  }
}

.template {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-template-areas: "one" "two" "three";
  gap: 2rem;
  padding-block: 3.1111111111rem 12.7777777778rem;
  padding-inline: 1.3333333333rem;
}
@media (min-width: 40em) {
  .template {
    padding-block: 3.1111111111rem 22.2222222222rem;
    padding-inline: 2.7777777778rem;
  }
}
.template__card {
  flex-flow: column nowrap;
  border-radius: 0.8333333333rem;
  box-shadow: 0 8px 30px rgba(193, 200, 209, 0.477);
  padding-block: 2.8888888889rem;
  padding-inline: 2.5555555556rem;
  text-align: center;
}
@media (min-width: 40em) {
  .template__card {
    flex-flow: row-reverse nowrap;
    gap: 4rem;
    text-align: left;
    padding-left: 8.0555555556rem;
  }
  .template__card:nth-of-type(2) {
    flex-flow: row nowrap;
  }
}
.template__hero {
  margin-bottom: 3.6666666667rem;
}
@media (min-width: 40em) {
  .template__hero {
    max-width: 45%;
    margin: 0;
  }
}
.template__box {
  flex: 1 1 50%;
}
@media (max-width: 39.9375em) {
  .template__box {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
  }
}
.template__heading {
  font-size: min(max(1.25rem, 2vw), 2.5rem);
  margin-bottom: 1rem;
}
.template__text {
  font-size: 0.8611111111rem;
}
@media (min-width: 40em) {
  .template__text {
    font-size: 1rem;
  }
}

.cta {
  font-weight: 600;
  font-size: 0.7777777778rem;
  width: 15.2777777778rem;
  border-radius: 100px;
  background-color: #ff52bf;
  box-shadow: 0 8px 30px rgba(152, 157, 164, 0.477);
  border: 0 solid none;
  color: white;
  padding: 0.925em;
  transition: all 250ms;
}
.cta:hover {
  cursor: pointer;
  background-color: #ff99da;
}
.cta:active {
  transform: scale(0.95);
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.1);
}
@media (min-width: 40em) {
  .cta {
    font-size: 0.8888888889rem;
    padding: 1.05rem;
  }
}

.component {
  flex-flow: column nowrap;
  position: relative;
  bottom: -3.6111111111rem;
  border-radius: 0.8333333333rem;
  background-color: white;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.384);
  padding: 1.3333333333rem;
  margin: 0 auto;
  text-align: center;
  width: 22.2222222222rem;
  height: 9.8333333333rem;
}
@media (min-width: 40em) {
  .component {
    bottom: -5.5555555556rem;
    padding: 3.5555555556rem;
    width: 60vmax;
    height: 16.6666666667rem;
  }
}
.component__heading {
  font-size: 1rem;
  margin-bottom: 1.5555555556rem;
}
@media (min-width: 40em) {
  .component__heading {
    font-size: min(max(1.25rem, 2vw), 2.5rem);
    margin-bottom: 3rem;
  }
}

.social {
  font-size: 0.7777777778rem;
  border-radius: 50%;
  border: 1px solid #fff;
  width: 1.875rem;
  height: 1.875rem;
  color: #fff;
  margin: 0 0.5rem;
}